import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import infomationadd from '@/components/infomationadd'
import error from '@/components/error'
import success from '@/components/success'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:'/infomationadd',
      name:'infomationadd',
      component:infomationadd
    },
    {
      path:'/error',
      name:"error",
      component:error
    }
    ,
    {
      path:'/success',
      name:"success",
      component:success
    }
  ]
})



